
import { Options, Vue } from "vue-class-component";
import {v4} from 'uuid';
@Options({
  emits: ["addTodo"],
})
export default class Composer extends Vue {
  desc = "";

  addTodo(e: Event) {
    e.preventDefault();
    const todo = {
      id: v4(),
      desc: this.desc,
      done: false
    }
    this.desc = '';
    this.$emit('addTodo', todo);
  }
}
